"use client"

import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { ShoppingCart } from "lucide-react"
import Image from "next/image"
import { useParams } from "next/navigation"
import { useCart } from "@/context/CartContext"

const products = [
  {
    id: "1",
    name: "AMD Ryzen 9 5950X",
    category: "Processors",
    price: 799.99,
    image: "https://images.pexels.com/photos/1432675/pexels-photo-1432675.jpeg",
    description: "16-core processor for ultimate gaming and content creation",
    specs: {
      cores: "16 Cores / 32 Threads",
      baseSpeed: "3.4 GHz",
      boostSpeed: "Up to 4.9 GHz",
      cache: "72MB Total Cache",
      tdp: "105W",
    },
    features: [
      "AMD StoreMI Technology",
      "AMD Ryzen Master Utility",
      "Premium AMD Ryzen Desktop Processor",
    ],
    stock: 15,
  },
  {
    id: "2",
    name: "NVIDIA RTX 4080",
    category: "Graphics Cards",
    price: 1199.99,
    image: "https://images.pexels.com/photos/5474296/pexels-photo-5474296.jpeg",
    description: "High-end graphics card for 4K gaming and AI workloads",
    specs: {
      memory: "16GB GDDR6X",
      coreClock: "2.21 GHz",
      boostClock: "2.51 GHz",
      rayTracing: "3rd Generation RT Cores",
      dlss: "DLSS 3.0",
    },
    features: [
      "NVIDIA Ada Lovelace Architecture",
      "Real-time Ray Tracing",
      "AI-powered Performance",
    ],
    stock: 8,
  },
  {
    id: "3",
    name: "Corsair Vengeance RGB Pro",
    category: "Memory",
    price: 189.99,
    image: "https://images.pexels.com/photos/2582928/pexels-photo-2582928.jpeg",
    description: "32GB DDR4 RAM with RGB lighting",
    specs: {
      capacity: "32GB (2x16GB)",
      speed: "3600MHz",
      timing: "CL18",
      voltage: "1.35V",
      rgb: "10-Zone RGB Lighting",
    },
    features: [
      "Custom PCB for Better Signal Quality",
      "Tightly Screened Memory ICs",
      "Modern Aluminum Heat Spreader",
    ],
    stock: 20,
  },
]

export default function ProductPage() {
  const params = useParams()
  const { addItem } = useCart()
  const product = products.find((p) => p.id === params.id)

  if (!product) {
    return (
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-2xl font-bold">Product not found</h1>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
        {/* Product Image */}
        <div className="relative h-[400px] md:h-[500px]">
          <Image
            src={product.image}
            alt={product.name}
            fill
            sizes="(max-width: 768px) 100vw, 50vw"
            className="object-cover rounded-lg"
          />
        </div>

        {/* Product Info */}
        <div className="space-y-6">
          <div>
            <h1 className="text-3xl font-bold mb-2">{product.name}</h1>
            <p className="text-gray-600 mb-4">{product.category}</p>
            <p className="text-xl font-bold mb-4">${product.price}</p>
            <p className="text-gray-700">{product.description}</p>
          </div>

          <div className="space-y-4">
            <div className="flex items-center space-x-2">
              <span className="text-sm text-gray-600">
                Availability: {product.stock > 0 ? "In Stock" : "Out of Stock"}
              </span>
              {product.stock > 0 && (
                <span className="text-sm text-gray-600">
                  ({product.stock} units)
                </span>
              )}
            </div>

          <Button 
            className="w-full" 
            disabled={product.stock === 0}
            onClick={() => {
              addItem({
                id: parseInt(product.id),
                name: product.name,
                price: product.price,
                image: product.image
              })
            }}
          >
            <ShoppingCart className="mr-2 h-4 w-4" />
            Add to Cart
          </Button>
          </div>

          {/* Product Details Tabs */}
          <Tabs defaultValue="specs" className="w-full">
            <TabsList className="w-full">
              <TabsTrigger value="specs" className="flex-1">
                Specifications
              </TabsTrigger>
              <TabsTrigger value="features" className="flex-1">
                Features
              </TabsTrigger>
            </TabsList>
            <TabsContent value="specs">
              <Card>
                <CardContent className="p-4 space-y-2">
                  {Object.entries(product.specs).map(([key, value]) => (
                    <div key={key} className="flex justify-between">
                      <span className="text-gray-600 capitalize">
                        {key.replace(/([A-Z])/g, " $1").trim()}
                      </span>
                      <span>{value}</span>
                    </div>
                  ))}
                </CardContent>
              </Card>
            </TabsContent>
            <TabsContent value="features">
              <Card>
                <CardContent className="p-4">
                  <ul className="list-disc list-inside space-y-2">
                    {product.features.map((feature, index) => (
                      <li key={index} className="text-gray-700">
                        {feature}
                      </li>
                    ))}
                  </ul>
                </CardContent>
              </Card>
            </TabsContent>
          </Tabs>
        </div>
      </div>

      {/* Related Products */}
      <div className="mt-16">
        <h2 className="text-2xl font-bold mb-6">Related Products</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {products
            .filter((p) => p.id !== product.id)
            .slice(0, 3)
            .map((relatedProduct) => (
              <Card key={relatedProduct.id} className="hover:shadow-lg transition-shadow">
                <CardContent className="p-4">
                  <div className="relative h-48 mb-4">
                    <Image
                      src={relatedProduct.image}
                      alt={relatedProduct.name}
                      fill
                      sizes="(max-width: 768px) 100vw, (max-width: 1200px) 50vw, 33vw"
                      className="object-cover rounded-md"
                    />
                  </div>
                  <h3 className="font-semibold text-lg mb-2">{relatedProduct.name}</h3>
                  <p className="text-gray-600">${relatedProduct.price}</p>
                </CardContent>
              </Card>
            ))}
        </div>
      </div>
    </div>
  )
}
